    >

      <v-card v-if="teamDataDialog" class="pass-form">

        <a-card-title>افزودن به تیم</a-card-title>

        <v-card-text class="pt-6">

          <v-form ref="form">
            <v-row class="pa-0 py-6">
              <v-col class="py-0" cols="12" md="12">
                <v-text-field
                    background-color="white"
                    label="سمت"
                    @keypress.enter="addToTeam()"
                    :disabled="userEditLoading"
                    :loading="userEditLoading"
                    v-model="addTeamData.post"
                    outlined
                    color="txtf"
                    dense
                >
                </v-text-field>
              </v-col>
              <v-col class="py-0" cols="12" md="12">
                <v-text-field
                    background-color="white"
                    label="توضیحات"
                    @keypress.enter="addToTeam()"
                    :disabled="userEditLoading"
                    :loading="userEditLoading"
                    v-model="addTeamData.desc"
                    outlined
                    color="txtf"
                    dense
                >
                </v-text-field>
              </v-col>
            </v-row>
          </v-form>

        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
