          >
            افزودن به تیم
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <ProfileDialog :ddata="profileData" />
  </v-card>
</template>

<script>
import BlockItem from "@/components/Misc/BlockItem";
import axios from "axios";
import {validationMixin} from 'vuelidate'
import {email, minLength, required, sameAs} from "vuelidate/lib/validators";
import DeleteDialog from "@/components/Misc/DeleteDialog";
import {forceRerender} from "@/components/Misc/forceRerender";
import ASbtn from "@/components/Dashboard/Misc/Components/a-sbtn";
import ACardTitle from "@/components/Dashboard/Misc/Components/a-card-title";
import ProfileDialog from "@/components/Misc/ProfileDialog";

export default {
  created() {
    this.usersLoading = true;
    this.rolesLoading = true;
    this.fetchTable();
    this.fetchTeamTable();
    this.fetchRoles();
    this.fetchPermissions();
  },
  mixins: [validationMixin,forceRerender],
  name: "UsersApp",
  data() {
    return {
      profileData : {
        dialog : false,
        idSelected : null,
      },
      changepassDialog : false,
      changepassTitle : null,
      changepassId : null,
      old_password : null,

      roleTitleLoading: false,
      roleTitle: null,

      usersLoading: false,


      rolesLoading: false,
      permsToRoleDialog: false,
      permtoroleLoading: false,
      permsToRoleSelected: [],
      permsToRoleId: null,
      permsToRoleTitle: null,
      permissions: [],
      permissionsModel: null,


      userAddDialog: false,
      userEditLoading: false,
      username: null,
      password: null,
      email: null,
      repeatPassword: null,
      loading: false,
      data: null,
      usernameRules: {
        pattern: value => {
          const pattern = /^(?=.{6,20}$)(?![_.0-9])(?!.*[_.]{2})[a-zA-Z0-9._]+(?<![_.])$/
          return pattern.test(value) || 'نام کاربری باید حاوی حروف مجاز و حداقل 6 کاراکتر باشد'
        },
      },
      passshow: false,
      passshow2: false,

      role: null,
      userUpdateDialog: false,
      first_name: null,
      last_name: null,
      userItem: null,


      deleteData: {
        api : '',
        dialog: false,
        loading: false,
        idSelected: null,
      },
      roleDeleteData: {
        api : '',
        dialog: false,
        loading: false,
        idSelected: null,
      },


      users: {
        data: []
      },
      teamUsers: {
        data: []
      },
      teamDataDialog : false,
      addTeamData : {
        post : null,
        desc : null,
        id : null,
      },
      rolesData: []
    }
  },
  components: {ProfileDialog, ACardTitle, ASbtn, DeleteDialog, BlockItem},
  validations: {
    password: {
      required,
      minLength: minLength(6)
    },
    repeatPassword: {
      sameAsPassword: sameAs('password')
    },
    username: {
      required,
    },
    role: {
      required,
    },
    email: {
      required,
      email
    },

  },
  watch: {
    userAddDialog(val) {
      if (!val) {
        this.username = null;
        this.password = null
        this.email = null
        this.repeatPassword = null
        this.first_name = null;
        this.last_name = null;
        this.role = null;
      }
    },
    userUpdateDialog(val) {
      if (!val) {
        this.username = null;
        this.password = null
        this.email = null;
        this.repeatPassword = null
        this.first_name = null;
        this.last_name = null;
        this.role = null;
      }
    }
  },
  methods: {
    toSeeProfile (id) {
      this.profileData.idSelected = id;
      this.profileData.dialog = true;
    },
    addToTeam () {
      this.usersLoading = true;
      this.loading = true;
      this.teamDataDialog = false;
      axios.post(this.$store.state.host + '/accounts/api/v1/add-my-team/'+this.addTeamData.id,{
        post : this.addTeamData.post,
        desc : this.addTeamData.desc
      },{
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(()=>{
        this.notify('اضافه شد','info');
        this.addTeamData.post = null;
        this.addTeamData.desc = null;
        this.fetchTeamTable();
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(() => {
        this.usersLoading = false;
        this.loading = false;
      })
    },
    DeleteTeamUser(id){
      this.usersLoading = true;
      this.loading = true;
      axios.delete(this.$store.state.host + '/accounts/api/v1/remove-my-team/'+id,{
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(()=>{
        this.notify('حذف شد','info');
        this.fetchTeamTable();
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(() => {
        this.usersLoading = false;
        this.loading = false;
      })
    },
    AddTeamUser(id){
      this.addTeamData.id = id;
      this.teamDataDialog = true;
    },
    toChangePass(item){
      this.changepassId = item.id;
      this.changepassTitle = item.username;
      this.changepassDialog = true;
    },
    updatePass() {
      this.$v.$touch();
      if (this.$v.password.minLength && this.$v.password.required && this.$v.repeatPassword.sameAsPassword) {
        this.userEditLoading = true;
        axios.patch(this.$store.state.api + 'user-change-password/'+this.changepassId, {
          old_password : this.old_password,
          new_password : this.password,
        }, {
          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },
        }).then(() => {
          this.$store.state.notify.color = 'orange';
          this.$store.state.notify.is = true;
          this.$store.state.notify.text = "رمز تغییر یافت";
          this.password = null;
          this.old_password = null;
          this.repeatPassword = null;
        }).catch(error=>{
          this.errorHandle(error.response)
        }).finally(() => {
          this.changepassDialog = false;
          this.userEditLoading = false;
        });
      }
    },
    addRole() {
      this.$refs.role.validate();
      if(this.$refs.role.validate()) {

        this.roleTitleLoading = true;
        const title = this.roleTitle;
        this.roleTitle = null;
        axios.post(this.$store.state.api + 'add-role/', {
          title,
          permissions: [],
        }, {
          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },
        }).then(() => {
          this.forceRerender();
          this.fetchRoles();
        }).finally(() => {
          this.roleTitleLoading = false;
        }).catch(error=>{
          this.errorHandle(error.response)
        })
      }
    },
    updateUser() {
      this.$v.$touch();
      if (this.$v.username.required && this.$v.email.required && this.$v.email.email && this.usernameValidate) {
        this.userEditLoading = true;
        axios.patch(this.$store.state.api + 'user-update/'+this.userItem, {
          role: this.role,
          username: this.username,
          email: this.email,
          first_name: this.first_name,
          last_name: this.last_name
        }, {
          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },
        }).then(() => {
          this.$store.state.notify.color = 'orange';
          this.$store.state.notify.is = true;
          this.$store.state.notify.text = "ویرایش انجام شد.";
          this.userUpdateDialog = false;
          this.fetchTable();
        }).finally(() => {
          this.userEditLoading = false;
        }).catch(error=>{
          this.errorHandle(error.response)
        });
      }
    },
    signup() {
      this.$v.$touch();
      if (this.$v.username.required && this.$v.email.required && this.$v.email.email && this.usernameValidate && this.$v.password.minLength && this.$v.password.required && this.$v.repeatPassword.sameAsPassword) {
        this.userEditLoading = true;
        axios.post(this.$store.state.api + 'register/', {
          username: this.username,
          email: this.email,
          password: this.password
        }).then(() => {
          this.$store.state.notify.color = 'success';
          this.$store.state.notify.is = true;
          this.$store.state.notify.text = "ثبت نام انجام شد.";
          this.userAddDialog = false;
          this.fetchTable();
        }).finally(() => {
          this.userEditLoading = false;
        }).catch(error=>{
          this.errorHandle(error.response)
        });
      }
    },
    toUpdateUser(item) {
      this.userItem = item.id;
      this.email = item.email;
      this.username = item.username;
      if (item.role) {
        this.role = item.role.id;
      }

      this.first_name = item.first_name;
      this.last_name = item.last_name;
      this.userUpdateDialog = true;
    },
    toDelete(id,api) {
      this.deleteData.idSelected = id;
      this.deleteData.api = api;
      this.deleteData.dialog = true;
    },
    toRoleDelete(id,api) {
      this.roleDeleteData.idSelected = id;
      this.roleDeleteData.api = api;
      this.roleDeleteData.dialog = true;
    },

    updateRole() {
      this.permtoroleLoading = true;


      axios.put(this.$store.state.api + 'update-role/' + this.permsToRoleId, {
        title: this.permsToRoleTitle,
        permissions: this.permsToRoleSelected
      }, {
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(() => {
        this.fetchRoles();
      }).finally(() => {
        this.permtoroleLoading = false;
        this.permsToRoleDialog = false;
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    },
    permsToRole(item) {
      this.permsToRoleId = item.id;
      this.permsToRoleTitle = item.title;
      this.permsToRoleDialog = true;
      this.permsToRoleSelected = [];
      for (let i in item.permissions) {
        this.permsToRoleSelected[i] = item.permissions[i].toString();
      }
    },
    fetchPermissions() {
      this.permtoroleLoading = true;
      axios.get(this.$store.state.api + 'permissions/', {
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response => {

        this.permissions = response.data.data;
      }).finally(() => {
        this.permtoroleLoading = false;
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    },
    fetchTable() {
      this.usersLoading = true;
      this.loading = true;
      axios.get(this.$store.state.api + 'users/', {
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response => {

        this.users = response.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(() => {
        this.usersLoading = false;
        this.loading = false;
      })
    },
    fetchTeamTable() {
      this.usersLoading = true;
      this.loading = true;
      axios.get(this.$store.state.host + '/accounts/api/v1/my-team/', {
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response => {
        this.teamUsers = response.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(() => {
        this.usersLoading = false;
        this.loading = false;
      })
    },
    fetchRoles() {
      this.rolesLoading = true;
      axios.get(this.$store.state.api + 'roles/', {
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response => {
        this.rolesData = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(() => {
        this.rolesLoading = false;
      })
    },
  },
  computed: {
    usernameValidate() {
      return this.usernameRules.pattern(this.username) === true;
    },
    passErrors() {
      const errors = []
      if (!this.$v.password.$dirty) return errors
      !this.$v.password.minLength && errors.push('پسورد باید دارای حداقل 6 کاراکتر باشد')
      !this.$v.password.required && errors.push('پسورد را وارد کنید')
      return errors
    },
    samepassErrors() {
      const errors = []
      if (!this.$v.repeatPassword.$dirty) return errors
      !this.$v.repeatPassword.sameAsPassword && errors.push('تکرار پسورد همخوانی ندارد')
      return errors
    },
    usernameErrors() {
      const errors = []
      if (!this.$v.username.$dirty) return errors
      !this.$v.username.required && errors.push('نام کاربری را وارد کنید')
      return errors
    },
    emailErrors() {
      const errors = []
      if (!this.$v.email.$dirty) return errors
      !this.$v.email.email && errors.push('ایمیل را به شکل صحیح وارد کنید')
      !this.$v.email.required && errors.push('ایمیل را وارد کنید')
      return errors
    },
    teamHeaders() {
      return [
        {
          text: 'نام کاربری',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'user.username',
        },
        {
          text: 'مسئولیت', class: 'header-table-gray',
          align: 'center',
          value: 'post',
        },
        {
          text: 'توضیحات', class: 'header-table-gray',
          align: 'center',
          value: 'desc',
        },
        {
          text: 'عملیات', class: 'header-table-gray',
          align: 'center',
          value: 'id',
        },
      ]
    },
    headers() {
      return [
        {
          text: 'نام کاربری',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'username',
        },
        {
          text: 'نقش', class: 'header-table-gray',
          align: 'center',
          value: 'role',
        },
        {
          text: 'ایمیل', class: 'header-table-gray',
          align: 'center',
          value: 'email',
        },
        {
          text: 'عملیات', class: 'header-table-gray',
          align: 'center',
          value: 'id',
        },


      ]
    },
    rolesHeaders() {
      return [
        {
          text: 'نام نقش',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'title',
        },
        {
          text: 'عملیات', class: 'header-table-gray', align: 'center',
          value: 'id',
        },


      ]
    },
  }
}
</script>



